/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useCallback, useState } from 'react';
import { Header } from 'components/atoms';
import classNames from 'classnames';
import { BurgerButton } from '../burger-button/burger-button';

interface MenuTopProps {
  logo?: React.ReactNode;
  children?: React.ReactNode | React.ReactNode[];
}

const MenuTop = ({ logo, children }: MenuTopProps) => {
  const [isVisible, setIsVisible] = useState(false);
  const toggleVisible = useCallback(() => {
    setIsVisible((visible) => !visible);
  }, []);

  return (
    <Header>
      <a href="/" className="flex items-center">
        {logo}
      </a>
      <div className="flex md:order-2">
        <BurgerButton onClick={toggleVisible} isVisible={isVisible} />
      </div>
      <div
        className={`${classNames({ hidden: !isVisible })} justify-between items-center w-full md:flex md:w-auto md:order-1`}
        id="navbar-sticky"
      >
        <ul className="flex flex-col w-full p-4 mt-4 rounded-lg md:flex-row md:space-x-8 md:mt-0 md:font-medium md:border-0 h-full">
          {children}
        </ul>
      </div>
    </Header>
  );
};

export { MenuTop };
